:root {
	--font-size: 16px;
	--font-family: "sans-serif";
	--font-color: #fff;
	--bg-color: #343a40;
	--main-color: #f54910;
	--second-color: #f02d7d;
	--third-color: #19d719;
	--pad-btn: #5a6268;
	--general-spacing: 1rem;
}

@font-face {
	font-family: "Splatoon";
	src: local("Splatoon"), url(/CN--Pearl/static/media/Splatoon.cf96c398.otf) format("opentype");
}

@font-face {
	font-family: "Splatoon2";
	src: local("Splatoon2"), url(/CN--Pearl/static/media/Splatoon2.e96c1074.otf) format("opentype");
}

html {
	font-size: 16px;
	font-size: var(--font-size);
	font-family: "sans-serif";
	font-family: var(--font-family);
	color: #fff;
	color: var(--font-color);
	background-color: #343a40;
	background-color: var(--bg-color);
}

body {
	background-image: url(data:image/png;base64,);
	margin: 0;
	padding: 0;
	color: #fff;
	color: var(--font-color);
	font-family: "Splatoon2";
}

main {
	min-height: 100vh;
}

.main-color,
.primary {
	background-color: #f54910;
	background-color: var(--main-color);
}

.second-color,
.secondary {
	background-color: #f02d7d;
	background-color: var(--second-color);
}

.third-color,
.tertiary {
	background-color: #19d719;
	background-color: var(--third-color);
}

.pad-btn {
	background-color: #5a6268;
	background-color: var(--pad-btn);
}

.btn-secondary:hover {
	background-color: #19d719;
	background-color: var(--third-color);
}

.header {
	margin-bottom: 1.5rem;
}

.spacing {
	margin: 1rem 0;
}

.highlight {
	color: #f02d7d;
	color: var(--second-color);
}

.highlight:hover {
	color: #19d719;
	color: var(--third-color);
}

a {
	color: #fff;
	color: var(--font-color);
}

a:hover {
	color: #fff;
	color: var(--font-color);
	font-weight: bold;
	text-decoration: none;
}

.nav-item > .dropdown-menu.show {
	background-color: #f02d7d;
	background-color: var(--second-color);
}

.nav-item > .dropdown-menu.show > .dropdown-item {
	background-color: #f02d7d;
	background-color: var(--second-color);
	color: #fff;
	color: var(--font-color);
}

.nav-item > .dropdown-menu.show > .dropdown-item:hover {
	font-weight: bold;
}

.navbar-toggler {
	border: 0.25px solid #fff;
	border: 0.25px solid var(--font-color);
}

.navbar-toggler-icon {
	background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255, 255, 255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.main-container {
	padding: 1.5rem;
	border-radius: 0.5rem;
}

.modal-header {
	border: none;
}

.modal-content {
	border: none;
	border-radius: 0.5rem;
}

.modal-footer {
	padding-top: 0;
}

.modal-body,
.modal-footer {
	background-color: #343a40;
	background-color: var(--bg-color);
	border: none;
}

.modal-open {
	padding: 0;
}

.custom-control-input:checked ~ .custom-control-label::before {
	color: #fff;
	border-color: #19d719;
	border-color: var(--third-color);
	background-color: #19d719;
	background-color: var(--third-color);
}

.table-responsive {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.feature {
	background-color: #343a40;
	background-color: var(--bg-color);
	border-radius: 0.5rem;
	padding: 1rem;
}

.profile-img {
	max-width: 250px;
	width: auto;
  height: auto;
  max-height: 250px;

}

.tab-body {
	background-color: #343a40;
	background-color: var(--bg-color);
	border-radius: 0 0 0.5rem 0.5rem;
	padding: 1rem;
}

.nav.nav-tabs,
.nav-tabs .nav-link:hover,
.nav-tabs .nav-link:focus,
.nav-tabs > .nav-item > .active,
.nav-tabs > .nav-item {
	border: none;
}

.nav-tabs > .nav-item {
	background-color: #343a40;
	background-color: var(--bg-color);
	border-radius: 0.5rem 0.5rem 0 0;
}

.nav-tabs > .nav-item > .active {
	background-color: #19d719;
	background-color: var(--third-color);
	color: #fff;
	color: var(--font-color);
}

table th, .table td{
	vertical-align: middle;
}
